import * as React from 'react';
import { Grid, Col, Row, Popover, ButtonToolbar, Button } from 'react-bootstrap';

export class Error extends React.Component<any, any> {
    render() {
        return (
            <Grid>
                <Row className="show-grid">
                    <Col xs={6} md={4}>
                        <div className="img-with-text">
                            <img src="baby-cow.jpg" alt="Baby Cow" />
                            <p>source: neko-kuma.deviantart.com</p>
                        </div>
                    </Col>
                    <Col xs={12} md={8}>
                        <div style={{ height: 120 }}>
                            <Popover
                                id="popover-basic"
                                placement="right"
                                positionTop={50}
                                title="Oops!">
                                Something is wrong. But, you see, I'm just a baby, so I can't deal
                                with everything in this cruel world! You wanna help me?
                                <ButtonToolbar>
                                    <Button href="https://github.com/mdamyanova/ASP.NET-Core-ReactJS-Example" target="_blank">help me</Button>
                                    <Button href='/'>ignore me</Button>
                                </ButtonToolbar>
                            </Popover>
                        </div>
                    </Col>
                </Row>
            </Grid>
        );
    }
}


